import React from "react";
// @Mui
import { Box, Container, IconButton, Stack, Tooltip } from "@mui/material";
//
import Logo from "../Logo";
import HeaderSearch from "./HeaderSearch";
import HeaderLink from "./HeaderLink";
import { Icon } from "@iconify/react";

// ---------------------------------------------------------------------

function Header() {
  const HEADER_LINKS = [
    { title: "Home", href: "/" },
    { title: "Products", children: [], href: "/products" },
    { title: "Collection", href: "/collection" },
    { title: "About Us", href: "/about-us" },
  ];

  return (
    <Box
      sx={{
        bgcolor: "background.paper",
        py: 2,
        position: "fixed",
        top: 0,
        zIndex: 10,
        width: "100%",
      }}
    >
      <Container>
        <Stack
          direction="row"
          justifyContent="space-between"
          alignItems="center"
        >
          <Box sx={{ width: 200 }}>
            <Logo />
          </Box>

          {/* Header Links */}
          <Stack direction="row" alignItems="center" gap={3}>
            {HEADER_LINKS.map((_, index) => (
              <HeaderLink key={index} title={_.title} href={_.href} />
            ))}
          </Stack>
          {/* Actions */}
          <Stack direction="row" alignItems="center" gap={1}>
            <Tooltip title="Cart">
              <IconButton>
                <Icon icon="proicons:cart" style={{ width: 30, height: 30 }} />
              </IconButton>
            </Tooltip>
            <Tooltip title="Menu">
              <IconButton>
                <Icon icon="jam:menu" style={{ width: 30, height: 30 }} />
              </IconButton>
            </Tooltip>
            <Tooltip title="Search Product">
              <IconButton>
                <Icon icon="icon-park-outline:search" />
              </IconButton>
            </Tooltip>
          </Stack>
        </Stack>
      </Container>
    </Box>
  );
}

export default Header;
